<template>
  <div class="share-activity-detail full-screen">
    <div class="page-panel">
      <!-- <pem-scroll-view class="scroll-view" ref="scroll"> -->
        <div class="container" :class="{'contanier-addpadding': addPaddingBottom}">
          <div class="banner" v-if="result.pic"><img :src="(rootImgUrl + result.pic)" alt=""></div>
          <div class="basic-msg">
            <h3 class="title">{{ result.title }}</h3>
            <p class="time">{{ result.begintime }} - {{ result.endtime }}</p>
            <p class="address">{{ result.address }}</p>
          </div>
          <div class="detail-msg">
            <ul>
              <li v-if="result.introduce">
                <h4 class="detail-title"></h4>
                <p class="detail-desc">{{ result.introduce }}</p>
              </li>
              <li v-if="result.agenda">
                <h4 class="detail-title"></h4>
                <p class="detail-desc">{{ result.agenda }}</p>
              </li>
              <li v-if="result.institution">
                <h4 class="detail-title"></h4>
                <p class="detail-desc">{{ result.institution }}</p>
              </li>
            </ul>
            <div class="poster" v-if="result.poster"><img :src="(rootImgUrl + result.poster)" alt=""></div>
          </div>
          <div class="sign-wrapper" v-if="!isWeixin && result.status == 100">
            <div class="title"></div>
            <div class="second-title"></div>
            <div class="sign-methods">
              <span class="sign-method sign-method-radio" :class="{'on': signtype === 100}" @click="changeSigntype"></span>
              <span class="sign-method sign-method-radio" :class="{'on': signtype === 200}" @click="changeSigntype"></span>
            </div>
            <div class="sign-msgs" v-if="signtype === 100">
              <ul class="sign-msgs-ul" v-if="signobj.activityId == 14">
                <li class="sign-msgs-li">
                  <div class="sign-msgs-li-title"> ()<span>*</span></div>
                  <label class="sign-msgs-li-input">
                    <div class="sign-methods sign-methods2">
                      <div class="sign-method sign-method-checkbox" :class="{'on': signcontent.indexOf('814') > -1}" @click="changeSigncontent('814')">814</div>
                      <div class="sign-method sign-method-checkbox" :class="{'on':  signcontent.indexOf('815') > -1}" @click="changeSigncontent('815')">815</div>
                      <div class="sign-method sign-method-checkbox" :class="{'on':  signcontent.indexOf('816 ') > -1}" @click="changeSigncontent('816 ')">816 </div>
                      <div class="sign-method sign-method-checkbox" :class="{'on':  signcontent.indexOf('816 1V1') > -1}" @click="changeSigncontent('816 1V1')">816 1V1</div>
                    </div>
                  </label>
                </li>
              </ul>
              <div class="second-title sign-msg-title"></div>
              <ul class="sign-msgs-ul">
                <li class="sign-msgs-li">
                  <div class="sign-msgs-li-title"><span>*</span></div>
                  <label class="sign-msgs-li-input">
                    <input v-model.trim="signobj.name" @input="signInuptChange" @focus="signInuptFocus" @blur="signInuptBlur" type="text" maxlength="15" placeholder=""/>
                  </label>
                </li>
                <li class="sign-msgs-li">
                  <div class="sign-msgs-li-title"><span>*</span></div>
                  <label class="sign-msgs-li-input">
                    <input v-model.trim="signobj.phone" @input="signInuptChange"  @focus="signInuptFocus" @blur="signInuptBlur" type="text" maxlength="11" placeholder=""/>
                  </label>
                </li>
                <li class="sign-msgs-li">
                  <div class="sign-msgs-li-title"><span>*</span></div>
                  <label class="sign-msgs-li-input">
                    <input v-model.trim="signobj.company" @input="signInuptChange" @focus="signInuptFocus" @blur="signInuptBlur" type="text" maxlength="30" placeholder=""/>
                  </label>
                </li>
                <li class="sign-msgs-li">
                  <div class="sign-msgs-li-title"><span>*</span></div>
                  <label class="sign-msgs-li-input">
                    <input v-model.trim="signobj.position" @input="signInuptChange"  @focus="signInuptFocus" @blur="signInuptBlur" type="text"  maxlength="20" placeholder=""/>
                  </label>
                </li>
                <li class="sign-msgs-li">
                  <div class="sign-msgs-li-title"><span>*</span></div>
                  <label class="sign-msgs-li-input">
                    <input v-model.trim="signobj.email" @input="signInuptChange"  @focus="signInuptFocus" @blur="signInuptBlur" type="text" placeholder=""/>
                  </label>
                </li>
              </ul>
            </div>
            <div class="sign-notes" v-if="signtype === 200">
              <div class="sign-notes-title"></div>
              <div class="sign-notes-intro">App</div>
              <div class="sign-notes-img1"></div>
              <div class="sign-notes-intro"></div>
              <div class="sign-notes-img2"><img src="./sign_qrcode@2x.png" alt=""></div>
            </div>
          </div>
          <div class="action-btn-wrapper" v-if="!isWeixin && signtype === 100">
            <pem-button v-if="result.status == 100" @submit="signSubmit()" :isDisable="signbtnDisabled" text=""></pem-button>
            <pem-button  v-if="result.status != 100" :text="result.status == 700 ? '' : ''" isDisable="true"></pem-button>
          </div>
        </div>
      <!-- </pem-scroll-view> -->
      <div class="footer-open" @click="judgeGuide" v-if="!isWeixin && showBottom">
        <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
        <p>APP</p>
        <div class="btn"></div>
      </div>
      <div v-if="isWeixin" class="footer-guide">
        <pem-button class="guide-download-btn" @submit="goDownload()" text=""></pem-button>
        <pem-button  class="guide-open-btn" @submit="goSign()" text="APP"></pem-button>
      </div>
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
    </div>
    <pem-loading v-show="showLoading" title="~"></pem-loading>
    <pem-toast ref="toast" :text="toastMsg"></pem-toast>
    <pem-toast class="toast-success" ref="toastSuccess" text="3"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemButton from 'components/pem-button/pem-button'
import PemToast from 'components/pem-toast/pem-toast'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'

export default {
  data () {
    return {
      rootImgUrl: ImgPath,
      isWeixin: device.isWeixin(),
      isIOS: device.isIOS(),
      addPaddingBottom: false,
      showLoading: false,
      showGuideMask: false,
      showBottom: true,
      toastMsg: '',
      signtype: 100,  // 100 , 200 
      signcontent: [],  // 1    2    31V1
      signbtnDisabled: true,
      signobj: {activityId: null, type: 200, topictype: null, name: '', phone: '', company: '', position: '', email: ''},
      result: {status: 100}  // 100 , 700 
    }
  },
  components: {
    PemScrollView,
    PemButton,
    PemToast,
    PemLoading
  },
  created () {
    this._initData()
    this.wxShare()
  },
  methods: {
    _initData () {
      this.signobj.activityId = this.$route.query.id
      if (!device.isWeixin() && device.isAndroid()) {
        window.location.href = `pemarket://www.pemarket.com.cn/attr?type=3&id=${this.$route.query.id}`
      }
      let options = {activityId: this.$route.query.id}
      this._getShareActivity(options)
    },
    _getShareActivity (options) {
      MiniSiteApi.getAppShareActivity(options).then(res => {
        this.result = res || {}
        setTitle(this.result.title)
        this.wxShare({
          title: this.result.title,
          content: this.result.introduce,
          imgUrl: this.result.pic ? (ImgPath + this.result.pic) : 'http://cdn.xcx.pemarket.com.cn/img_logo_44x44_default.png'
        })
        // setTimeout(() => {
        //   this.refresh()
        // }, 50)
      }).catch((error) => {
        console.error(error)
      })
    },
    showToast (msg) {
      this.toastMsg = msg
      this.$refs.toast.show()
    },
    changeSigntype () {
      this.signtype = (this.signtype === 100) ? 200 : 100
    },
    changeSigncontent (svalue) {
      if (this.signcontent.indexOf(svalue) > -1) {
        this.signcontent = this.signcontent.filter(item => item !== svalue)
      } else {
        this.signcontent.push(svalue)
      }
    },
    signInuptFocus () {
      this.addPaddingBottom = true
      this.showBottom = false
      // this.refresh()
    },
    signInuptBlur () {
      this.addPaddingBottom = false
      this.showBottom = true
      // this.refresh()
    },
    signInuptChange () {
      this.signobj.name = this.signobj.name.trim()
      this.signobj.phone = this.signobj.phone.trim()
      this.signobj.company = this.signobj.company.trim()
      this.signobj.position = this.signobj.position.trim()
      this.signobj.email = this.signobj.email.trim()
      if (this.signobj.name && this.signobj.phone && this.signobj.company && this.signobj.position && this.signobj.email) {
        this.signbtnDisabled = false
      } else {
        this.signbtnDisabled = true
      }
    },
    signSubmit () {
      this.signInuptChange()
      if (this.signcontent.length === 0 && +this.signobj.activityId === 14) {
        this.showToast('')
        return false
      }
      if (!this.signobj.name) {
        this.showToast('')
        return false
      }
      if (!this.signobj.phone) {
        this.showToast('')
        return false
      }
      if (!/^(1[3-9])\d{9}$/.test(this.signobj.phone)) {
        this.showToast('')
        return false
      }
      if (!this.signobj.company) {
        this.showToast('')
        return false
      }
      if (!this.signobj.position) {
        this.showToast('')
        return false
      }
      if (!this.signobj.email) {
        this.showToast('')
        return false
      }
      if (!/^([0-9A-Za-z-_.]+)@([a-zA-Z0-9_-]+.[a-zA-Z0-9_-]+(.[a-zA-Z0-9_-]+)?)$/.test(this.signobj.email)) {
        this.showToast('')
        return false
      }
      this.signobj.topictype = this.signcontent.join(',')
      this.showLoading = true
      MiniSiteApi.insertAppShareActivity(this.signobj).then(res => {
        this.showLoading = false
        if (+res.resultcode === 1) {
          this.$refs.toastSuccess.show()
        } else {
          let msg = {
            '2': ',~',
            '3': ''
          }[res.resultcode] || ''
          this.showToast(msg)
        }
      }).catch((error) => {
        this.showLoading = false
        this.showToast('')
        console.error(error)
      })
    },
    goDownload () {
      if (this.isIOS) {
        window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
      } else {
        window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2'
      }
    },
    goSign () {
      if (this.isIOS) {
        window.location.href = `https://go.pemarket.com.cn/download-guide?type=3&id=${this.$route.query.id}`
      } else {
        this.showGuideMask = true
      }
    },
    judgeGuide () {
      if (device.isWeixin()) {
        if (device.isIOS()) {
          window.location.href = `https://go.pemarket.com.cn/download-guide?type=3&id=${this.$route.query.id}`
        } else {
          this.showGuideMask = true
        }
      } else {
        this.$router.push('/download')
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    refresh () {
      this.$refs.scroll.refresh()
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .share-activity-detail
    background: #ffffff
    .page-panel
      position: static
    .toast-success
      width: 80%
      max-width: 100%
      white-space: normal  
    .guide-open-browser
      position: fixed
      left: 0
      top: 0
      right: 0
      bottom: 0
      z-index: 1000
      opacity: 0.7
      background: #000000
      .guide-img
        width: 5.12rem
        height: 2.54rem
        margin: 0.48rem auto 0
        bg-image('./bg_guide')
        background-size: 100% 100%
        background-repeat: no-repeat
    .container
      padding-bottom: 1.3rem
      &.contanier-addpadding
        padding-bottom: 4rem
      .banner
        font-size: 0
        img
          width: 100%
      .basic-msg
        padding: 0.3rem 0.3rem 0.4rem
        .title
          font-family: PingFangSC-Medium
          font-size: 0.36rem
          font-weight: bold
          color: #41403E
          line-height: 0.5rem
          padding-bottom: 0.3rem
        p
          font-family: PingFangSC-Regular
          font-size: 14px
          color: #535251
          line-height: 0.4rem
          padding-left: 0.28rem
          background-size: 0.24rem 0.24rem
          background-repeat: no-repeat
          background-position: left center
          &.time
            bg-image('./bg_time')
            margin-bottom: 0.2rem
          &.address
            bg-image('./bg_address')
      .detail-msg
        padding: 0 0.3rem
        border-top: 0.1rem solid #F6F6FA
        .detail-title
          font-family: PingFangSC-Medium
          font-size: 0.36rem
          font-weight: bold
          color: #333333
          line-height: 0.5rem
          padding: 0.3rem 0 0.2rem
        .detail-desc
          font-family: PingFangSC-Regular
          font-size: 0.28rem
          color: #666666
          line-height: 0.42rem
          padding-bottom: 0.3rem
        .poster
          font-size: 0
          img
            width: 100%
      .sign-wrapper
        padding:  0.6rem 0.3rem 0
        .title
          padding-bottom: 0.3rem 
          font-family: PingFangSC-Medium
          font-size: 0.36rem
          font-weight: bold
          color: #333333
          line-height: 0.5rem
        .second-title
          font-family: PingFangSC-Medium
          font-size: 0.32rem
          font-weight: bold
          color: #333333
          line-height: 0.44rem
          &.sign-msg-title
            padding-top: 0.4rem
        .sign-methods
          font-size: 0
          padding-top: 0.3rem
          &.sign-methods2
            padding-top: 0
          .sign-method
            padding-left: 0.5rem
            background-size: 0.3rem 0.3rem
            background-repeat: no-repeat
            background-position: left center
            font-family: PingFangSC-Regular
            font-size: 0.3rem
            color: #333333
            line-height: 0.48rem
            &.sign-method-radio
              bg-image('./sign_type')
              &.on
                bg-image('./sign_type_on')
              &:last-of-type
                margin-left: 1rem
            &.sign-method-checkbox
              bg-image('./sign_content')
              margin-bottom: 0.2rem
              &.on
                bg-image('./sign_content_on')
              &:last-of-type
                margin-bottom: 0
        .sign-msgs-ul
          .sign-msgs-li-title
            padding: 0.3rem 0 0.2rem
            font-family: PingFangSC-Regular
            font-size: 0.3rem
            color: #333333
            line-height: 0.48rem
            span
              color: #FFC440
              vertical-align: top
              font-size: 0.24rem
              margin-left: 0.04rem
          .sign-msgs-li-input
            display: block
            input
              box-sizing: border-box
              display: block
              width: 100%
              height: 0.8rem
              padding: 0 0.2rem
              font-size: 0.24rem
              background: #F8F8F8
              border-radius: 0.04rem
        .sign-notes
          font-size: 0
          text-align: center
          padding-top: 0.5rem
          .sign-notes-title
            padding: 0.1rem 0 0.4rem
            font-family: PingFangSC-Medium;
            font-size: 0.28rem
            font-weight: bold
            color: #333333;
            line-height: 0.4rem
          .sign-notes-intro
            font-family: PingFangSC-Medium
            font-size: 0.3rem
            font-weight: bold
            color: #333333
            line-height: 0.42rem
          .sign-notes-img1
            display: inline-block
            margin: 0.4rem auto 0.6rem
            width: 2.02rem
            height: 3.44rem
            background-size: 100% 100%
            background-repeat: no-repeat
            bg-image('./sign_note')
          .sign-notes-img2
            display: inline-block
            margin: 0.3rem auto 0.4rem
            width: 2.1rem
            height: 2.1rem
            font-size: 0
            img
              width: 100%
              height: 100%
      .action-btn-wrapper
        padding: 0.2rem 0.7rem 0.3rem
        margin-top: 0.46rem
        .pem-submit
          margin: 0 auto
          &.disable
            background-color: #dedede
            color: #333333
    .footer-open, .footer-guide
      position: fixed
      left: 0
      bottom: 0
      right: 0
      height: 1.2rem
      border-top: 0.5px solid #f3f3f3
      background: #ffffff
      .logo
        position: absolute 
        left: 0.24rem
        top: 0.2rem
        height: 0.8rem
        width: 0.8rem
        border-radius: 0.08rem;
      p
        position: absolute 
        left: 1.21rem
        top: 0.4rem
        font-family: PingFangSC-Medium
        font-size: 0.28rem
        font-weight: bold
        color: #666666
        line-height: 0.4rem 
      .btn
        position: absolute
        top: 0.32rem
        right: 0.22rem
        width: 1.56rem
        height: 0.6rem
        line-height: 0.6rem
        background: #FFC131
        border-radius: 0.04rem
        font-family: PingFangSC-Regular
        font-size: 0.3rem
        color: #41403E
        letter-spacing: -0.3px
        text-align: center
    .footer-guide
      z-index: 100
      height: 1.4rem
      .pem-submit
        width: 3.3rem
        position: absolute
        top: 0.3rem
        margin: 0
        font-size: 0.28rem
      .guide-download-btn
        box-sizing: border-box
        left: 0.3rem
        border: 0.5px solid #41403E
        background: #ffffff
      .guide-open-btn
        right: 0.3rem
</style>


